<template>
  <div class="flex flex-col">
    <div class=" m-2 flex justify-between flex-wrap">
      <div class="ml-5 flex bg-blue-100 p-2 rounded-xl">
        <div>
          <span class=" text-xs">Ingrese el Codigo de Barras</span>
          <img
            src="https://static.vecteezy.com/system/resources/previews/001/199/360/non_2x/barcode-png.png"
            class=" w-24"
          />
        </div>

        <div class=" flex items-end text-xs">
          <input
            id="txtidcodigo"
            v-model="codigo_barra"
            type="number"
            class="p-3 bg-gray-100 rounded-xl block w-20"
            @keyup.enter="confirmAutomatico()"
          />
        </div>
      </div>

      <div class=" flex flex-col">
        <h1 class=" text-2xl font-medium">Recepción de Documentos</h1>
      </div>
    </div>

    <section>
      <div class="text-center flex flex-col lg:flex-row lg:flex-wrap">
        <div
          class=" relative flex flex-col mx-2 border p-3 rounded-lg mt-2 lg:mt-0"
          v-for="(item, i) in data_list"
          :key="i"
        >
          <div
            class=" flex flex-col text-left text-xs w-full"
            style="width: 240px !important;"
          >
            <div class=" mt-5 border border-black  p-2 flex flex-col">
              <span
                class=" text-lg text-center font-medium uppercase cursor-pointer"
                >{{ `${item.nombre}` }}</span
              >

              <div class="h-px bg-black"></div>

              <span class=" font-medium mt-2">
                <i class="fas fa-user text-green-500 mr-2"></i>
                {{ item.dni }}</span
              >

              <span class=" font-medium mt-2">
                <i class="fas fa-calendar text-green-500 mr-2"></i>
                {{ item.fecha_recepcion }}</span
              >

              <div
                class="flex justify-center items-center text-center mt-2 cursor-pointer"
              >
                <img :src="`${base_photo}${item.foto}`" class="foto" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>

    <vs-dialog
      width="550px"
      not-center
      blur
      v-model="dialogConfirm"
      class=" z-50"
    >
      <template #header>
        <h4 class="not-margin">Recepcionar <b>Documento del Postulante</b></h4>
      </template>

      <div class="con-content">
        <p>
          Esta seguro que desea recepcionar el documento del postulante? <br />
        </p>
      </div>

      <template #footer>
        <div class="flex justify-between">
          <div></div>
          <div class="flex">
            <vs-button
              id="btn_aceptar"
              autofocus
              @click="recepcionar_documento()"
              transparent
            >
              Si
            </vs-button>
            <vs-button @click="dialogConfirm = false" dark transparent>
              Cancelar
            </vs-button>
          </div>
        </div>
      </template>
    </vs-dialog>
  </div>
</template>

<script>
import axios from "@//plugins//axios.js";

export default {
  data() {
    return {
      codigo_barra: "",
      dialogConfirm: false,
      data_list: [],
    };
  },

  computed: {
    get_auth() {
      return this.$store.getters["auth/get_auth"];
    },

    base_photo() {
      let host =
        process.env.NODE_ENV == "development"
          ? process.env.VUE_APP_BASE_URL
          : process.env.VUE_APP_URL;

      host = host.replace("/api/v1/", "/media/");
      return host;
    },
  },

  mounted() {
    this.set_sede().then(() => {
      setTimeout(() => {
        this.focusss("txtidcodigo");
      }, 200);
    });

    this.get_lista();
  },

  methods: {
    get_lista() {
      const is_sede = this.get_auth.group == "sede";
      let sede = "";
      if (is_sede) sede = this.get_auth.username;

      axios.get("lista_recepcionados", { params: { sede } }).then((resp) => {
        this.data_list = resp.data;
      });
    },

    confirmAutomatico() {
      this.dialogConfirm = true;
      setTimeout(() => {
        document.getElementById("btn_aceptar").click();
        this.dialogConfirm = false;
      }, 100);
    },

    focusss(id) {
      document.getElementById(id).focus();
    },

    async set_sede() {
      try {
        const { data } = await axios.get("cede");
        this.sede_data = data;
      } catch (error) {
        return null;
      }
    },

    recepcionar_documento() {
      if (this.codigo_barra == null || this.codigo_barra == 0) {
        return;
      }

      const is_sede = this.get_auth.group == "sede";
      let sede = "";
      if (is_sede) sede = this.get_auth.username;

      axios
        .get("recepcionar_documento_postulante", {
          params: { postulante: this.codigo_barra, sede },
        })
        .then((resp) => {
          if (resp.status == 200) {
            this.$vs.notification({
              title: "Postulante",
              text: "Documento recepcionado correctamente! 😎",
              color: "success",
            });
            this.codigo_barra = null;
            this.focusss("txtidcodigo");
            this.get_lista();
            return;
          }

          this.$vs.notification({
            title: "Postulante",
            text: "No existe el número de expendiente! 😢",
            color: "danger",
          });
          this.codigo_barra = null;
          this.focusss("txtidcodigo");
        })
        .catch(() => {
          this.$vs.notification({
            title: "Postulante",
            text: "No existe el número de expendiente! 😢",
            color: "danger",
          });
          this.codigo_barra = null;
          this.focusss("txtidcodigo");
        });
    },
    //
  },
};
</script>

<style lang="scss" scoped>
.foto {
  width: 240px !important;
  height: 288px !important;
}
</style>
